<template>
  <div class="fireCase">
      <!--消防概况 组件复用 -->
     <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                    <h3>
                      {{fireCases.title}}
                    </h3>
                    <ul>
                      <li ref="fireChart">

                      </li>
                    </ul>
                </div>
         </el-col>
     </el-row>


  </div>
</template>

<script>

export default {
  name: 'fireCase',

  data () {
    return {
        fireCases:{
            title:'消防综合概况',
            num:[70, 34, 60, 78],
            name:['高危项', '危险项', '一般项', '正常项']
        }
    }
  },

  mounted(){
    
        var fireChart = this.$refs.fireChart;   
        // this.tables.forEach((table,index) => {
            // console.log(index)
                // 图表初始化
        var myChart1 = this.$echarts.init(fireChart);

        
        // 配置数据
       var data =  this.fireCases.num//数据
        var titlename = this.fireCases.name;
        var valdata = []
        var myColor = ['#eb245d','#8f3acb','#3b5ccd','#3b91cc'];
        var option = {
            textStyle:{
                fontSize:16,
            },
            backgroundColor: '#090e14',
            xAxis: {
                show: false,
            },
            grid: {  
                left: '1%',  
                right: '0%',  
                bottom: '-15%',  
                top:'2%',
                containLabel: true,
                
            } ,
            yAxis: [{
                show: true,
                data: titlename,
                inverse: true,
                axisLine: {
                    show: false
                },
                splitLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    color: '#fff',
                    // formatter: function(value, index) {//索引值
                    //     return [
                    //         '{lg|' + (index + 1) + '}' + '{title|' + value + '} '
                    //     ].join('\n')
                    // },
                    fontSize:16,
                    rich: {
                        lg: {
                            backgroundColor: '#339911',
                            color: '#fff',
                            borderRadius: 5,
                            // padding: 5,
                            align: 'center',
                            width: 5,
                            height: 5
                        },
                    }
                },
            }, 
            {
                show: true,
                inverse: true,
                data: valdata,
                axisLabel: {
                    textStyle: {
                        fontSize: 12,
                        color: '#fff',
                    },
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },

            }
            ],
            series: [{
                name: '条',
                type: 'bar',
                yAxisIndex: 0,
                data: data,
                barWidth: 6,//小棒高
                
                itemStyle: {
                    normal: {
                        barBorderRadius: 8,
                        color: function(params) {
                            var num = myColor.length;
                            return myColor[params.dataIndex % num]
                        },
                    }
                },
                label: {
                    normal: {
                        show: true,
                        position: 'right',
                        // formatter: '{c}%',
                        
                    }
                },
            },
             {
                name: '框',
                type: 'bar',
                yAxisIndex: 1,
                barGap: '-50%',
                data: [100, 100, 100, 100],
                barWidth: 10,
        
                itemStyle: {
                    normal: {
                        color: 'none',
                        // borderColor: '#00c1de',//外围边框
                        borderWidth: 1,
                        barBorderRadius: 8,
                    }
                }
            },
             ]
        };
                            
        
        // 生成图表 myChart是个数组 
        myChart1.setOption(option);   
        window.addEventListener("resize",function(){
      	 myChart1.resize();
                	}); 
        // })
                    
  }
  
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.fireCase{
      >.el-row{
        >.el-col{
            background: @background-color;
          >.grid-content{
              height: 274px;
              >h3{
                padding: 1.25rem 0 0 0.625rem;
                font-size: 22px;
              }
            >ul{
              font-size: 1.125rem;
              padding: 6% 7%;
              flex-wrap:wrap;
              display:flex;
              >li{
                    flex-grow:1;
                    flex-basis:394px;
                    height: 166px;
                    text-align: center;
                    margin-bottom:0.625rem;
                    flex-shrink:1;
                    padding: 10px;
              }

            }
          }
        }
      }
    
  }

</style>






