<template>
  <div class="deviceSituation">
      <el-row>
         <el-col>
             <!-- 设备情况描述 -->
             <div class="grid-content bg-purple">
                <ul  v-for="deviceSituation in deviceSituations" :key="deviceSituation.id">
                    <li>
                        <i class="icon-device" :style="{backgroundPosition:deviceSituation.position}"></i>
                        <div>
                            <span>{{deviceSituation.name}}</span>
                        </div>
                    </li>
                    <li>
                        <div>
                            {{deviceSituation.num}} 
                        </div>
                        <span>{{deviceSituation.title1}}</span>
                    </li>
                    <li>
                        <div>
                            {{deviceSituation.rate1}}<span></span>{{deviceSituation.rate2}}
                        </div>
                        <span>{{deviceSituation.title2}}</span>
                    </li>
                    <li>
                        <span>{{deviceSituation.btn}}</span>
                    </li>
                </ul>

             </div>
            
         </el-col>
     </el-row>
  </div>
</template>

<script>

export default {
  name: 'deviceSituation',
  data () {
    return {
        deviceSituations:[
            {position:"-107px 0px",name:"电气",num:"1",title1:"本周报警次数",rate1:0,rate2:10,title2:"正常值",btn:"导出"},
            {position:"-107px -76px",name:"感烟",num:"1",title1:"本周报警次数",rate1:0,rate2:10,title2:"正常值",btn:"导出"},
            {position:"-107px -152px",name:"燃气",num:"1",title1:"本周报警次数",rate1:0,rate2:10,title2:"正常值",btn:"导出"},
            {position:"-107px -228px",name:"液位",num:"1",title1:"本周报警次数",rate1:0,rate2:10,title2:"正常值",btn:"导出"},
        ],

    }
  },



}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.deviceSituation{
    >.el-row{
        >.el-col{
            
          >.grid-content{
            >ul{
                background:@background-color;
                margin-bottom: 0.625rem;
                font-size: 1.125rem;
                flex-wrap:wrap;
                display:flex;
              >li{
                    text-align: center;
                    flex-shrink:1;
                    flex-grow: 2;
                    padding: 50px 0;
              }
              >li:nth-of-type(1){ 
                //   flex-basis:10rem;
                  flex-grow: 1;
                  border-right: 1px solid #161e29;
                >i{
                  width: 2.875rem;
                  height: 3.125rem;
                  display: inline-block; 
                }
                >div{
                  margin-top: 20px;
                }
              }
              >li + li>div{
                font-size: 60px;
                color:rgba(138,151,168,.8);
                margin: 0px 0 6px 0;

              }            
              >li:nth-of-type(3){
                  >div{
                      font-size: 28px;
                      color:@color2;
                      letter-spacing: 6px;
                      line-height: 68px;
                      >span::before{
                        content: "";
                        vertical-align: middle;
                        display: inline-block;
                        width: 2px;
                        height: 46px;
                        background-color: #8996a7;
                        margin: 0 12px;
                      }
                  }
              }
              >li:nth-of-type(4){
                  span{
                    // width: 140px;
                    // height: 44px;
                    // line-height: 44px;
                    padding: 10px 50px;
                    border-radius: 6px;
                    border-top:2px solid #2e3b4e; 
                    display: inline-block;
                    background-color: #1a232c;
                    margin-top:26px;
                    cursor: pointer;
                  }
              }

            }
          }
        }
      }
}


</style>






