<template>
  <div class="bigMap">
      <!-- 组件复用 -->
     <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                  <div  id="mapContainer" @click="getPoint"></div>
                  <ul>
                    <li v-for="alarmMap in alarmContent.alarmMaps" :key="alarmMap.id">
                      <span>{{alarmMap.title}}</span>
                      <span>{{alarmMap.num}}</span>
                    </li>
                  </ul>
              </div>
         </el-col>
     </el-row>

  </div>
</template>

<script>
// var Event = require('././event')
import { mapGetters } from 'vuex'

export default {
  name: 'bigMap',
  data () {
    return {
      // 数据放在中央管理器 
  
    }
  },
  // 监听map点击位置
  // watch:{
  //   middata:{
  //     updateposition(val,oldVal){ 
  //       console.log("更新的：" + val.middata,val.oldVal)
  //     },
  //     deep:true
  //   }
  // },

  computed:{
    //获取中央状态管理数据
    ...mapGetters([
      'alarmContent',
      'coords',
      'mapPoints',
      'center',
      'level',
    ]),
  },

  methods:{
    // 触发中央mutations方法
    getSum(){
      this.$store.commit('getSum');
    },
    getPoint(){
      var upPoint = sessionStorage.getItem("mapPointsKey");
      // console.log(upPoint + "获值21");
      this.$store.dispatch("updatePoint",upPoint);//触发actions里并传参

    }
  },

  created(){
    this.getSum();
  },

  mounted(){
    this.$Event.handlerMap("mapContainer",this.center.lng, this.center.lat,this.level,this.coords);
    
  } ,

}


</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

  .bigMap{
      .el-row{
        .el-col{
          .grid-content{
            position: relative;
            >#mapContainer{
              height: 578px;
              >div:nth-of-type(7){
                >span{display:none !important;}
              }
              
            }
            >ul{
              padding: 0.7rem 0;
              background-color: rgba(9, 14, 20,.95);
              position: absolute;
              top:0;
              width: 100%;
              flex-wrap:wrap;
              display:flex;
              >li{
                flex-grow:1;
                flex-basis:5.5rem;
                text-align: center;
                flex-shrink:1;
                font-size: 1.125em;
              }
              >li:nth-of-type(1){
                >span:nth-of-type(1){
                  color:#4e50c1;
                }
              }
              >li:nth-of-type(2){
                >span:nth-of-type(1){
                  color:#047cb8;
                }
              }
              >li:nth-of-type(3){
                >span:nth-of-type(1){
                  color:#730a5f;
                }
              }
              >li:nth-of-type(4){
                >span:nth-of-type(1){
                  color:#cf2c2f;
                }
              }
            }
          }
        }
        
      }
  }


</style>

<style>

.BMap_cpyCtrl {
    display:none;
}
.anchorBL a img{
    display:none;
    /* opacity: 0.1; */
}


/*map左下角icon*/ 
/* .anchorBL .BMap_geolocationIcon{
  background-color: rgba(250,250,250,1) !important;
  background: url("../../assets/images/picPoint.jpg") no-repeat center !important;
}
.anchorBL .BMap_geolocationContainer{
  border: 1px solid #3a4958 !important;
  border-radius: 6px !important;
  padding: 8px !important;
  width: 48px;
  height: 48px !important;
  background-color: #090d16;
} */

.BMap_Marker:nth-of-type(1){
  width: 60px !important;
  height: 60px !important;
  border-radius: 50%;
  animation:cartoon 3s infinite linear alternate;
}

@keyframes cartoon{
  0%{box-shadow: 0px 0px 5px 1px #127abb; background-color:rgba(18,122,187,.1);}
}

</style>




