<template>
  <div class="alarmMap">
      <!-- 组件复用 -->
     <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                  <div  id="mapBox"></div>
                  <ul>
                    <li v-for="alarmMap in alarmContent.alarmMaps" :key="alarmMap.id">
                      <span>{{alarmMap.title}}</span>
                      <span>{{alarmMap.num}}</span>
                    </li>
                  </ul>
                </div>
         </el-col>
     </el-row>

  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'alarmMap',
  data () {
    return {

    }
  },

  computed:{
    //获取中央状态管理数据
    ...mapGetters([
      'alarmContent',
      'coords',
      'center',
      'level',
    ]),
  },

  methods:{

  },

  mounted(){
    // 调用已配的事件
    this.$Event.handlerMap("mapBox",this.center.lng, this.center.lat,this.level,this.coords)
  }

  
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

  .alarmMap{
      .el-row{
        .el-col{
          .grid-content{
            >#mapBox{
              height: 294px;
            }
            position: relative;
            // min-height: 294px;
            >ul{
              padding: 0.7rem 0;
              background-color: rgba(9, 14, 20,.9);
              position: absolute;
              top:0;
              width: 100%;
              flex-wrap:wrap;
              display:flex;
              >li{
                flex-grow:1;
                flex-basis:5.5rem;
                text-align: center;
                flex-shrink:1;
                font-size: 1.125em;
              }
              >li:nth-of-type(1){
                >span:nth-of-type(1){
                  color:#4e50c1;
                }
              }
              >li:nth-of-type(2){
                >span:nth-of-type(1){
                  color:#047cb8;
                }
              }
              >li:nth-of-type(3){
                >span:nth-of-type(1){
                  color:#730a5f;
                }
              }
              >li:nth-of-type(4){
                >span:nth-of-type(1){
                  color:#cf2c2f;
                }
              }
            }
          }
        }
        
      }
  }


</style>





