<template>
  <div class="device">
     <el-row :gutter="10">
         <el-col v-for="deviceNum in deviceNums" :key="deviceNum.id">
             <!-- 可视化图表 -->
            <div ref="chartBox">
                
            </div>
            <ul>
                <li v-for="condition in deviceNum.conditions" :key="condition.id">
                    <div>{{condition.num}}</div>
                    <span>{{condition.title}}</span>
                </li>
            </ul>
         </el-col>
     </el-row>


  </div>
</template>

<script>

export default {
  name: 'device',
  props:["deviceNums"],
  data () {
    return {
        
    }
  },

  mounted(){
    
    var chartBox = this.$refs.chartBox;  //获取元素 复


        this.deviceNums.forEach((deviceNum,index) => {
            // 图表初始化
            var myChart = this.$echarts.init(chartBox[index]);
            // console.log(myChart)
            
            // 配置数据
            var option = {
                    series: [
                            {
                            type: 'pie',
                            radius: ['90%', '100%'],
                            center:['50%','50%'],
                            color:this.deviceNums[index].color1,
                            
                            label: {
                                normal: {
                                    position: 'center'
                                }
                            },
                            data: [
                                {
                                value: 6,
                                // name: '故障及未检修数',
                                
                                label: {
                                    normal: {
                                        // formatter: '{d} %',
                                        textStyle: {
                                            fontSize: 18,
                                            color:'#fff',
                                        }
                                    }
                                },
                                // tooltip:{
                                //     // trigger: 'item',
                                //     // formatter: "{a} <br/>计算公式:占比率=({b}/设备总数)*100%<br/> 吊销注销数 : {c}"
                                // }
                            },
                            {
                                value: 47,
                                // name: '安全设备',
                                label: {
                                    normal: {
                                        formatter: this.deviceNums[index].numIcon,//每个圈里的名称
                                        textStyle: {
                                            color: '#fff',
                                            fontSize: 16
                                        }
                                    }
                                },
                                itemStyle: {
                                    normal: {
                                        color: this.deviceNums[index].color2
                                    },
                                    emphasis: {
                                        color: this.deviceNums[index].color2,
                                    }
                                },           
                            }]
                        },
                    ]
                };

                // 生成图表 myChart是个数组 
                myChart.setOption(option);    
        });


                
                   
  }
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.device{
    .el-row{
        display:flex;
        .el-col{
            height: 274px;
            background-color: @background-color;
            position: relative;
            margin-right: 0.625rem;
            overflow: hidden;
            >div{
                width: 108px;
                height: 108px; 
                border-radius: 54px;
                margin: 0 auto;
                margin-top: 40px;
                min-width: 90px;
            }
            ul{
                border-top:@border-top;
                font-size: 16px;
                display: flex;
                flex-wrap:wrap;
                position: absolute;
                bottom: 0;
                width: 100%;
                li{
                    text-align: center;
                    flex: auto;
                    align-items: center;
                    padding-top: 18px;
                    div{
                        font-size: 22px;
                    }
                    height: 80px;

                }
            }
        }
        
    }
}




</style>






