<template>
  <div class="todayPatrol">
      <!--今日巡查 组件复用 -->
     <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                    <h3>
                      {{todayPatrols.title}}
                    </h3>
                    <ul>
                      <li v-for="todayPatrol in todayPatrols.patrols" :key="todayPatrol.id">
                        <h2>
                          {{todayPatrol.num}}
                        </h2>
                        <span>{{todayPatrol.title}}</span>
                      </li>
                    </ul>
                </div>
         </el-col>
     </el-row>

  </div>
</template>

<script>

export default {
  name: 'todayPatrol',
  props:["todayPatrols"],
  data () {
    return {
      
    }
  },

  
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.todayPatrol{
      >.el-row{
        >.el-col{
            background: @background-color;
          >.grid-content{
              padding: 1.25rem 0 0 0.625rem;
              height: 274px;
              >h3{
                font-size: 22px;
              }
            >ul{
              font-size: 1.125rem;
              padding: 12% 0;
              flex-wrap:wrap;
              display:flex;
              >li{
                    flex-grow:1;
                    flex-basis:5.5rem;
                    text-align: center;
                    margin-bottom:0.625rem;
                    flex-shrink:1;
              }
              >li>h2{
                background-color: transparent;
                box-shadow: 0 0 0 0 transparent;
                font-size: 60px;
                color:rgba(138,151,168,.8);
                margin: -10px 0 40px 0;
              }
            }
          }
        }
      }
    
  }

</style>






