<template>
  <div class="security">
      <!--安防概况 组件复用 -->
     <el-row>
         <el-col>
              <div class="grid-content bg-purple">
                    <h3>
                      {{securityContent.securityTitle}}
                    </h3>
                    <ul>
                      <li>
                        <label class="icon-security" :style="{backgroundPosition:securityContent.securityLevel.position}"></label>
                        <h2>
                          <span>{{securityContent.securityLevel.title}}</span><span>{{securityContent.securityLevel.level}}</span>
                        </h2>
                      </li>
                      <li v-for="security in securityContent.securitys" :key="security.id">
                        <h2>
                          {{security.rate}}
                          <span>{{security.percent}}</span>
                        </h2>
                        <span>{{security.title}}</span>
                      </li>
                    </ul>
                </div>
         </el-col>
     </el-row>


  </div>
</template>

<script>

export default {
  name: 'security',
  props:["securityContent"],
  data () {
    return {
      // 数据已在父页传值 这里不要了
        // securityContent:{
        //     securityTitle:"安防概况",
        //     securityLevel:{position:"0 -135px",title:"安防评级",level:"优秀"},
        //     securitys:[
        //         {rate:"99",title:"设备完好率"},
        //         {rate:"9",title:"故障检修率"}
              
        //     ]
        // },
    }
  },

  
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.security{
      >.el-row{
        >.el-col{
            background: @background-color;
          >.grid-content{
              padding: 1.25rem 0 0 0.625rem;
              height: 294px;
              >h3{
                font-size: 22px;
              }
            >ul{
              font-size: 1.125rem;
              padding: 6% 0;
              flex-wrap:wrap;
              display:flex;
              >li{
                    flex-grow:1;
                    flex-basis:5.5rem;
                    text-align: center;
                    margin-bottom:0.625rem;
                    flex-shrink:1;
              }
              >li:nth-of-type(1){ 
                >label{
                  width: 2.875rem;
                  height: 3.125rem;
                  display: inline-block; 
                }
                >h2{
                  font-size: 1.125rem;
                  margin-top: 44px;
                  >span:nth-of-type(1){
                      margin-right: 30px;
                  }
                  >span:nth-of-type(2){
                    color:#6bdba3;
                  }
                }
              }
              >li + li>h2{
                background-color: transparent;
                box-shadow: 0 0 0 0 transparent;
                font-size: 60px;
                color:rgba(138,151,168,.8);
                margin: -10px 0 40px 0;
                >span{
                  font-size:1.5625rem;
                  border: none;
                }
              }
            }
          }
        }
      }
    
  }

</style>






