<template>
  <div class="alarmTypeEchart">
      <el-row>
         <el-col>
             <!-- 告警类型分布 可视化图表 -->
            <div ref="tableBox" style="width:900px; height:274px; ">

            </div>
         </el-col>
     </el-row>
  </div>
</template>

<script>

export default {
  name: 'alarmTypeEchart',

  data () {
    return {
        // 这里要留个json形式在这   不然网页图表会消失
        tables:[
                    ['name','6.8','6.9','6.10','6.11','6.12','6.13','6.14','6.15','6.16'],
                    ['电器', 2, 2, 5, 5, 5, 6, 8, 9, 10],
                    ['烟感',0, 0, 1, 2, 3, 3, 3, 5, 12],
                    ['燃气',2, 3, 4, 4, 5, 6 , 8, 10, 11],
                    ['压力变送器',2, 3, 6, 6, 6, 6, 6, 8, 11],
                ]
    }
  },

   mounted(){
    
        var chartBox1 = this.$refs.tableBox;   

        this.tables.forEach((table,index) => {
            // console.log(index)
                // 图表初始化
        var myChart1 = this.$echarts.init(chartBox1);//主题色dark
        
        // console.log(myChart1)
        
        // 配置数据
       var option = {
           title:{
                text:this.title,
                textStyle:{//大标题
                    fontWeight:'normal',
                    color: 'rgba(255,255,255,.8)',
                    fontSize:22,
                    fontFamily:'"Helvetica Neue", Helvetica, Microsoft Yahei, Hiragino Sans GB, WenQuanYi Micro Hei, sans-serif',
                },
                top:20,
                left:5
           },

           textStyle:{
               color: '#dedede',//坐标 整体 
           },
           
            tooltip : {
                trigger: 'axis',
                
            },

            grid: {//折线网格
                bottom: '7%',
                top:'24%',
                containLabel: true,
            },
            
            calculable : true,
            xAxis : [//x轴坐标 题
                {
                    type : 'category',
                    boundaryGap : false,//折线边界
                    // data : ['电器','烟感','燃气','压力变送器']    
                }
            ],
            yAxis : [//y轴坐标
                {
                    type : 'value',
                    axisLine: {show: true,},
                    splitLine: {
                        show: true,
                        lineStyle:{//y轴折线分割线调整
                            color:'#0e1621',
                            width: 1
                        }
                    },
                    
                }
            ],
            dataset:{
                // 提供一份数据  若一天的 看对应的列数值
                // source:[
                //     ['name','6.8','6.9','6.10','6.11','6.12','6.13','6.14','6.15','6.16'],
                    // ['电器', 2, 2, 5, 5, 5, 6, 8, 9, 10],
                    // ['烟感',0, 0, 1, 2, 3, 3, 3, 5, 12],
                    // ['燃气',2, 3, 4, 4, 5, 6 , 8, 10, 11],
                    // ['压力变送器',2, 3, 6, 6, 6, 6, 6, 8, 11],
                // ]
                source:this.tables //取上面data里属性值
            },
        
            series : [  
                
                {
                    type:'line',
                    color:['#dc2140'],
                    seriesLayoutBy: 'row',  //行列配置
                },
                {
                    type:'line',
                    color:'#1dcda7',
                    seriesLayoutBy: 'row'
                },
                {
                    type:'line',
                    color:'#035ee2',
                    seriesLayoutBy: 'row'
                },
                {
                    type:'line',
                    color:'#bb1e87',
                    seriesLayoutBy: 'row',
                },
                

            ]
        };
                            
        
        // 生成图表 myChart是个数组 
        myChart1.setOption(option);   
        window.addEventListener("resize",function(){
      	 myChart1.resize();
                	}); 
        })
                    
  }

}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.alarmTypeEchart{
    .el-row{
        .el-col{
            background-color: @background-color;
            display: flex;
            flex-wrap: nowrap;
            
            >.div{
                flex:auto;
            }
        
    }
    }
}




</style>






