<template>
  <div class="alarmTable">
      <el-row>
         <el-col>
             <!-- 告警类型 可视化图表 -->
            <div ref="tableBox" style="width:900px; height:274px; ">
                
            </div>
         </el-col>
     </el-row>
  </div>
</template>

<script>

export default {
  name: 'alarmTable',
  data () {
    return {
        title:"告警类型",
        // 这里要留个json形式在这   不然网页图表会消失
        tables:[
                ['name','10-11','10-12','10-18','10-20','10-21','10-22','10-23'],
                ['电器', 2, 3, 3, 5,6,7,9],
                ['烟感',0, 1, 5, 8,10,11,13],
                ['燃气',2, 3, 8, 11,12,15,16],
                ['压力变送器',3, 5, 8, 13,13,16,17],
        ],
        
    }
  },

   mounted(){
    
        var chartBox1 = this.$refs.tableBox;   

        this.tables.forEach((table,index) => {
            // console.log(index)
                // 图表初始化
        var myChart1 = this.$echarts.init(chartBox1);//主题色dark
        
        // console.log(myChart1)
        
        // 配置数据
       var option = {
           title:{
                text:this.title,
                textStyle:{//大标题
                    fontWeight:'normal',
                    color: 'rgba(255,255,255,.8)',
                    fontSize:22,
                    fontFamily:'"Helvetica Neue", Helvetica, Microsoft Yahei, Hiragino Sans GB, WenQuanYi Micro Hei, sans-serif',
                },
                top:20,
                left:5
           },

           textStyle:{
               color: '#dedede',//坐标 整体
            //    backgroundColor:'#090e14',   
           },
           
            tooltip : {
                trigger: 'axis',
                
            },
            legend: {//顶端指示
                // data:['电器','烟感','燃气','压力变送器'],
                top:20,
                textStyle:{
                    color:'rgba(255,255,255,.8)'
                }
            },
            grid: {//折线网格
                // left: '3%',
                // right: '3%',
                bottom: '7%',
                top:'24%',
                containLabel: true,
            },
            
            calculable : true,
            xAxis : [//x轴坐标 题
                {
                    type : 'category',
                    boundaryGap : false,//折线边界
                    // data : ['电器','烟感','燃气','压力变送器']    
                }
            ],
            yAxis : [//y轴坐标
                {
                    type : 'value',
                    axisLine: {show: true,},
                    // axisLabel: {show: true},//坐标轴标签显示否 一般会自动判断
                    splitLine: {
                        show: true,
                        lineStyle:{//y轴折线分割线调整
                            color:'#0e1621',
                            width: 1
                        }
                    },
                    
                }
            ],
            dataset:{
                // 提供一份数据  若一天的 看对应的列数值
                // source:[
                //     ['name','一天','一周','本月','三个月'],
                //     ['电器', 60, 132, 151, 284],
                //     ['烟感',0, 152, 191, 234],
                //     ['燃气',20, 32, 101, 154],
                //     ['压力变送器',120, 232, 301, 334],
                // ]
                source:this.tables //取上面data里属性值
            },
        
            series : [  
                
                {
                    type:'line',
                    color:['#dc2140'],
                    seriesLayoutBy: 'row',  //行列配置
                },
                {
                    type:'line',
                    color:'#1dcda7',
                    seriesLayoutBy: 'row'
                },
                {
                    type:'line',
                    color:'#035ee2',
                    seriesLayoutBy: 'row'
                },
                {
                    type:'line',
                    color:'#bb1e87',
                    seriesLayoutBy: 'row',
                },
                

            ]
        };
                            
        
        // 生成图表 myChart是个数组 
        myChart1.setOption(option);   
        window.addEventListener("resize",function(){
      	 myChart1.resize();
                	}); 
        })
                    
  }

}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.alarmTable{
    .el-row{
        .el-col{
            margin-left:0.35rem;
            background-color: @background-color;
            display: flex;
            flex-wrap: nowrap;

            >div{
                flex:auto;
                
            }
             
        }
    }
}




</style>






