import Vue from 'vue'
// 引入
import Vuex from 'vuex'

// 使用vuex
Vue.use(Vuex)

//搭建中央状态管理数据
export const store = new Vuex.Store({
    //设置属性
    state:{
        alarmContent:{
            alarmMaps:[
              {title:"在线",num:200},
              {title:"离线",num:2},
              {title:"故障",num:1},
              {title:"报警",num:15}
            ],
            
        },
        center: {lng:118.749831,lat: 31.944099},
        level:18,  
        coords:[
            {lng: 118.77807441,lat: 32.0572355,colo:"RGBA(238,10,21,.7)",sign:"red"},
            {lng: 118.78,lat: 32.04,colo:"RGBA(238,10,21,.7)",sign:"red"},
            {lng: 118.772,lat: 32.02,colo:"RGBA(238,10,21,.7)",sign:"red"},
            {lng: 118.77807331,lat: 32.0672255,colo:"RGBA(167,10,139,.7)",sign:"pink"},
            {lng: 118.7856,lat: 32.03,colo:"RGBA(7,114,170,.7)",sign:"blue"},
            {lng: 118.788,lat: 32.04,colo:"RGBA(87,90,275,.7)",sign:"purple"},
            {lng: 118.712,lat: 32.05,colo:"RGBA(238,10,21,.7)",sign:"red"},
        ],
        mapPoints:{"lng":"","lat":""},
    },
    // 获取属性的状态
    getters:{
        alarmContent:state => state.alarmContent,
        coords:state => state.coords,
        mapPoints:state => state.mapPoints,
        center:state => state.center,
        level:state => state.level,
    },
    //改变属性的状态
    mutations:{
        // 统计地图上标注情况数与地图顶端数一致
        getSum:state => {
            var redNum = 0,pinkNum = 0,blueNum = 0,purpleNum = 0;
            state.coords.forEach(coord => {
                if(coord.sign == "red"){
                    redNum += 1;
                    state.alarmContent.alarmMaps[3].num = redNum;
                }else if(coord.sign == "pink"){
                    pinkNum += 1;
                    state.alarmContent.alarmMaps[2].num = pinkNum;
                }else if(coord.sign == "blue"){
                    blueNum += 1;
                    state.alarmContent.alarmMaps[1].num = blueNum;
                }else if(coord.sign == "purple"){
                    purpleNum += 1;
                    state.alarmContent.alarmMaps[0].num = purpleNum;
                }
            
            })
        },
        updatePoint:(state,payload) => {//接收点击地图传来的经纬度参数
                console.log(payload)
                var a = JSON.stringify(payload);
                console.log(a)
                var b = JSON.parse(a);
                console.log(b.lat)
                // var obj = JSON.parse(payload);

                state.center = payload;
                
            
     
        }
        
    },
    // 应用mutations
    actions:{
        updatePoint:(context,payload) => {
            context.commit("updatePoint",payload);//接收点击地图传来的经纬度参数
        }
    }

})


