<template>
  <div class="alarmSummary">
      <!--告警概况  组件复用 -->
      <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                  <h3>
                    {{alarmContent.alarmTitle}}
                  </h3>
                  <ul>
                    <li v-for="alarm in alarmContent.alarms" :key="alarm.id">
                      <h3>{{alarm.num}}
                        <span></span>
                      </h3>
                      <span>{{alarm.title}}</span>
                    </li>
                  </ul>
             </div>
            
         </el-col>
     </el-row>
  </div>
</template>

<script>

export default {
  name: 'alarmSummary',

  data () {
    return {
        alarmContent:{
            alarmTitle:"告警概况",
            alarms:[
                {num:29,title:"全部告警"},
                {num:23,title:"已处理"},
                {num:5,title:"处理中"},
                {num:1,title:"未处理"}
            ],
        }
    }
  },

  created(){
      // this.fetchData();
  },

  methods:{
      // fetchData(){
      //     axios.get("https://www.iot-m.com/api/alarm/detail")
      //         .then(res => console.log(res.data))

      //     // fetch("https://www.iot-m.com/api/alarm/detail")
      //     //     .then(res => {
      //     //             return res.json()
      //     //         })
      //     //     .then(data => {
      //     //         console.log(data)
      //     // })

      // }
  },

  
   

}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";


.alarmSummary{
    .el-row{
      .el-col{
          background: @background-color;
          div.grid-content{
            height: 294px;//告警固高  缩放高可齐 隐藏
            overflow: hidden;
            padding: 1.25rem 0 0 0.625rem;
              >h3{
                font-size: 22px;
              }
              >ul{
                overflow: hidden;
                flex-wrap:wrap;
                display:flex;
                padding:13% 0 14% 0;
                >li{
                    float: left;
                    flex-grow:1;
                    flex-basis:5.5rem;
                    text-align: center;
                    margin-bottom:0.625rem;
                    flex-shrink:1;
                  >h3{
                    box-shadow:0 -1px 0px 1px #131f2f;
                    font-size: 3rem;  
                    background-color:#05080d;
                    border-radius:0.25rem; 
                    padding:10px 0;
                    margin:0 0.625rem 0.5rem 0;
                    position: relative;
                    
                    >span{
                      border:1px solid rgba(19,31,47,.8);
                      position: absolute;
                      display: inline-block;
                      width:100%;
                      top:50%;
                      left:0;
                      z-index: 100;
                    }
                  }
                  >span{
                    font-size: 1.125rem;
                    font-weight: 500;
                  }
              }
              }
          }
      }
    }
}


</style>






