<template>
  <div class="appIndex common-layout">
      <el-container>
          <el-main>
            <el-row :gutter="10">
              <el-col :span="6">
                <!-- 告警概况 组件 -->
                <alarmSummary></alarmSummary>
              </el-col>

              <el-col :span="6">
                <!--map 组件 -->
                <alarmMap></alarmMap>
              </el-col>

              <el-col :span="12">
                  <!-- 组件 -->
                <security :securityContent="securityContent"></security>
              </el-col>
            </el-row>

            <!-- 组件1 首页第二排 -->
            <el-row>
                <el-col :span="12" >
                  <device :deviceNums="deviceNums"></device>
                </el-col>
                <el-col :span="12">
                  <button class="timeBtn">
                    <!-- 时间选择器 -->
                    <timeSelector></timeSelector>
                  </button>
                  <alarmTable></alarmTable>
                </el-col>
            </el-row>
            
            <!-- 组件1 首页第三排 -->
            <el-row>
                <el-col :span="12" >
                  <device :deviceNums="deviceNums2"></device>
                </el-col>
                <el-col :span="12">
                  <button class="timeBtn">
                    <!-- 时间选择器 -->
                    <timeSelector></timeSelector>
                  </button>
                  <!-- 设计界面图形意义不清 这里临时占位  -->
                  <alarmTrend></alarmTrend>
                </el-col>
            </el-row>

            <!-- 第四排 -->
            <el-row :gutter="10">
              <el-col :span="12">
                  <!-- 大地图组件 -->
                <bigMap></bigMap>
              </el-col>
              <el-col :span=12>
                <el-row :gutter="10">
                  <el-col :span="12">
                    <!-- 告警组件 -->
                  <alarmSummary></alarmSummary>
                  </el-col>
                  <el-col :span="12">
                      <!-- 安防组件 -->
                    <security :securityContent="securityContent2"></security>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="24">
                    <device :deviceNums="deviceNums"></device>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

            <!-- 第五排 -->
            <el-row :gutter="10">
              <el-col :span="6">
                  <!-- 消防概况 -->
                  <fireCase></fireCase>
              </el-col>
              <el-col :span="6">
                <!-- 今日巡查 -->
                <todayPatrol :todayPatrols="todayPatrols"></todayPatrol>
              </el-col>
              <el-col :span="12" style="padding-left:0px;">
                  <button class="timeBtn">
                    <!-- 时间选择器 -->
                    <timeSelector></timeSelector>
                  </button>
                  <!-- 设计界面图形意义不清 这里临时占位  -->
                  <alarmTrend></alarmTrend>
              </el-col>
            </el-row>

            <!-- 第六排 -->
            <el-row :gutter="10">
              <el-col :span="6">
                <div class="grid-content bg-purple">
                  
                  <alarmType></alarmType>
                  <button class="timeBtn">
                    <!-- 时间选择器 -->
                    <timeSelector></timeSelector>
                  </button>
                  <!-- 表组件 -->
                  <alarmTypeEchart></alarmTypeEchart>
                  <ul>
                    <li>
                        <span>导出</span>
                    </li>
                  </ul>
                </div>
              </el-col>
              <el-col :span="18">
                <deviceSituation></deviceSituation>
              </el-col>
            </el-row>

          </el-main>
      </el-container>

  </div>
</template>

<script>
import Device from './Device'
import AlarmTable from './AlarmTable'
import AlarmSummary from './AlarmSummary'
import Map from './Map'
import BigMap from './BigMap'
import Security from './Security'
import AlarmTrend from './AlarmTrend'
import FireCase from './FireCase'
import TodayPatrol from './TodayPatrol'
import AlarmType from './AlarmType'
import DeviceSituation from './DeviceSituation'
import AlarmTypeEchart from './AlarmTypeEchart'
import TimeSelector from './TimeSelector'

export default {
  name: 'appIndex',
  components:{
    'device':Device,
    'alarmTable':AlarmTable,
    'alarmSummary':AlarmSummary,
    'alarmMap':Map,
    'security':Security,
    'bigMap':BigMap,
    'alarmTrend':AlarmTrend,
    'fireCase':FireCase,
    'todayPatrol':TodayPatrol,
    'alarmType':AlarmType,
    "deviceSituation":DeviceSituation,
    "alarmTypeEchart":AlarmTypeEchart,
    "timeSelector":TimeSelector,
  },
  data () {
    return {
      securityContent:{
            securityTitle:"安防概况",
            securityLevel:{position:"0 -135px",title:"安防评级",level:"优秀"},
            securitys:[
                {rate:"99",title:"设备完好率",percent:"%"},
                {rate:"9",title:"故障检修率",percent:"%"}
              
            ]
        },
        securityContent2:{
            securityTitle:"安防概况",
            securityLevel:{position:"0 -135px",title:"安防评级",level:"优秀"},
            securitys:[
                {rate:"99",title:"设备完好率",percent:"%"},
              
            ]
        },
         todayPatrols:{
            title:"今日巡查",
            patrols:[
                {num:"99",title:"消防员"},
                {num:"1",title:"巡查人数"},
                {num:"12",title:"巡查次数"},
              
            ]
        },
        deviceNums:[
            {
                numIcon:"全部设备",
                color1:"#1e4f6f",
                color2:"#3992ca",
                conditions:[
                    {num:53,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:1,title:"未检修"}
                ]
            },
            {
            numIcon:"烟感",
            color1:"#1f6668",
            color2:"#3bcbcb",
                conditions:[
                    {num:10,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:1,title:"未检修"}
                ]
            },
            {
            numIcon:"压力变送器",
            color1:"#203169",
            color2:"#395dcd",
            conditions:[
                    {num:14,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:1,title:"未检修"}
                ]
            }
          
        ],

        deviceNums2:[
            {
                numIcon:"电器线路",
                color1:"#48226b",
                color2:"#9139cd",
                conditions:[
                    {num:50,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:6,title:"未检修"}
                ]
            },
            {
            numIcon:"液位",
            color1:"#2c6f4e",
            color2:"#4dd290",
                conditions:[
                    {num:13,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:1,title:"未检修"}
                ]
            },
            {
            numIcon:"燃气",
            color1:"#66344d",
            color2:"#cb6191",
            conditions:[
                    {num:16,title:"总数"},
                    {num:5,title:"故障数"},
                    {num:2,title:"未检修"}
                ]
            }       
        ]
    }
  },


}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.el-row {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .bg-purple {
    background: @background-color;
  }

  .grid-content {
    min-height: 36px;
    // padding: 1.25rem 0 0 0.625rem;
  }


  .el-container{
    margin-top: 40px;
    padding: 0 1.25rem;//首页设置自定的整体的 3.125
    
    >.el-main{
      padding: 0;//充重置elment自带的
      overflow: hidden;//el自带auto干扰 消除下
      .el-row:nth-of-type(4){
        .el-col:nth-of-type(2){
          // 第四排设备右边间距调整
          .el-row:nth-of-type(2){
            margin-left:5px;
            margin-right:-10px;
          }
        }
      }
      .el-row:nth-of-type(5){
        overflow: hidden;  
        // 时间选择器
        .el-col:nth-of-type(3){
          position: relative;
          >button{
              position: absolute;
              top:20px;
              right:10px;
              z-index: 100;
              border-radius: 4px;
              
          }
        } 
      }
      // 时间选择器
      .el-row{
        .el-col:nth-of-type(2){
          position: relative;
          >button{
              position: absolute;
              top:20px;
              right:10px;
              z-index: 100;
              border-radius: 4px;
              
          }
        }
      }
      
      // 告警类型分布图左边的最下面一个按钮
      .el-row:nth-of-type(6){
          .el-col:nth-of-type(1){         
            >.grid-content{
                height: 826px;
                // 时间选择器样式调整
                position: relative;
                >button{
                    position: absolute;
                    top:70px;
                    left:10px;
                    z-index: 100;
                    border-radius: 4px;
                    
                }
            }
          }
          
          .el-col{
            ul{
            padding-top: 114px;
            padding-bottom: 40px;
            >li{
              display: flex;
              justify-content:center;
                span{
                  padding: 10px 50px;
                  border-radius: 6px;
                  border-top:2px solid #2e3b4e; 
                  display: inline-block;
                  background-color: #1a232c;
                  cursor: pointer;
                }
            }
          }
        }
      }
    }
    
  }


</style>






