<template>
  <div class="timeSelector">
      <!-- 时间选择器组件复用 -->
     <el-row>
         <el-col :span="12">
             <div class="grid-content bg-purple">
                    <div class="block">
                        <el-date-picker
                        v-model="value7"
                        type="daterange"
                        align="right"
                        unlink-panels
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        >
                        </el-date-picker>
                    </div>
                </div>
         </el-col>
     </el-row>

  </div>
</template>

<script>
export default {
  name: "timeSelector",
  data() {
    return {
        value7: ''

    };
  }
};
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.timeSelector {
    background-color:@background-color;
  .el-date-editor--daterange.el-input__inner {
    width: 215px;
    background: linear-gradient(#111d2b, #0c141f);
  }
  .el-range-editor--small.el-input__inner {
    height: 30px;
  }
  .el-input__inner {
    border: none;
  }
  
}
</style>

<style>
/*修改*/
.timeSelector .el-range-editor--small .el-range-input {
    background-color: transparent;
}
.timeSelector .el-date-editor .el-range-separator{
    width: auto;
}
.timeSelector .el-date-editor .el-range-input,.el-date-editor .el-range-separator{
    color: #dfe3e7;
}

</style>





