<template>
  <div class="appHeader common-layout">

    <el-container>
      <el-header style="height:auto;">
          <el-row>
            <el-col :span="12">
              <div class="grid-content">
                <i class="icon-logo"></i>
                <span>{{header.top.title}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="grid-content">
                <span>{{header.top.admin}}</span>
                <i class="icon-admin"></i>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
                <nav>
                  <ul>
                    <li :class="{activeStyle:nav.isActive}" v-for="(nav,index) in header.navs" :key="nav.id" @click="activeStyle(index)">{{nav.title}}</li>
                  </ul>
                </nav>
            </el-col>
          </el-row>
      </el-header>
    </el-container>

  </div>
</template>

<script>
export default {
  name: 'appHeader',
  data () {
    return { 
        header:{
          top:{title:"安防智能平台",admin:"Pukou"},
          navs:[{title:"概况面板",isActive:false},{title:"告警异常",isActive:false},{title:"终端设备",isActive:false},{title:"消防设施",isActive:false},{title:"监控视频",isActive:false}]
        },
        
    }
  },

  methods:{
    activeStyle(index){
      this.header.navs.forEach((nav) => {
        nav.isActive = false;
      })
      this.header.navs[index].isActive = true;
    }
  },

  // 初始化
  created(){
    this.header.navs[0].isActive = true;
  }
}
</script>


<style lang="less" scoped>
// @import "../assets/less/variable.less";

@import "../../static/css/variable.less";

.el-container{
    .el-header{
    background-color: @header-background-color;
    color: #333;
    line-height: 4.875rem;
    padding: 0 3.25rem;
    text-align: right;
    .el-row {
      color: @color;
      >.el-col:nth-of-type(1) {
        margin-bottom:-10px;
        div.grid-content{
          font-size: 1.6875rem;
          line-height: 78px;
          overflow: @overflow;
          .icon-logo{
            float: left;
            width: 3rem;
            height: 3rem;
            background-position:left top; 
            margin: 1.375rem 0 0 0.375rem;
          }
          span{
            float: left;
            margin: 0.625rem 0 0 1.25rem;
          }
        }
      }
      >.el-col:nth-of-type(2){
        >div.grid-content{
          cursor: pointer;
        }
      }
      
    }
    >.el-col:nth-of-type(2){
      text-align: right;
      >div.grid-content{
        text-align: right;
        
        >span{
          color: @color;
          font-family:Hiragino Sans GB, WenQuanYi Micro Hei;
        }
        overflow: @overflow;
      }
    }
 
  }
  
}
  

</style>

<style lang="less">
// @import "../assets/less/variable.less";

@import "../../static/css/variable.less";

.appHeader{
    .el-header{
        .el-row:nth-of-type(1){
          .el-col:nth-of-type(2){
              margin-bottom:-6px;
              div.grid-content{
                overflow: hidden;
                i.icon-admin{
                  float: right;
                  width:2.375rem;
                  height:2.375rem;
                  margin: 1.625rem 0 0 1.25rem;
                  background-position: 0px -78px;
                }
                span{
                    margin-top:6px;
                    display: inline-block;
                  }
              }
            }
        }


        .el-row:nth-of-type(2){
            .el-col{
              nav{
                line-height:67px;
                color:@color;
                margin-bottom:10px;
                ul{
                  overflow: @overflow;
                  li{
                    float: left;
                    padding: 0 3.27%;
                    font-size: 1.375rem;
                    cursor: pointer;
                    &:hover{
                      border-bottom: @border-bottom;
                    }
                  }
                    .activeStyle{
                      border-bottom: @border-bottom;
                    }
                }
              }
            }
        }
    }
}


</style>




