//模块  map配置

// 引入自定义模块
var customCoverings = require('./mapjs/customCoverings')


// 封装
function handlerMap(id,lng,lat,level,coords) {
    // 地图实例化
    var map = new BMap.Map(id);
    // Point创建一个坐标极点
    var point = new BMap.Point(lng,lat);
    // 地图初始化 同时设置地图展示级别
    map.centerAndZoom(point, level); 
    //开启鼠标滚轮缩放
    map.enableScrollWheelZoom(true);  
    // 左下角按钮定位
    map.addControl(new BMap.GeolocationControl());
      

    // 原始红点的标注
    // var marker = new BMap.Marker(point); //创建标注
    // map.addOverlay(marker); //将标注添到地图中

    function addMarker(point, index){  // 创建图标对象   
      var myIcon = new BMap.Icon("../static/images/marker.png", new BMap.Size(134, 134), {    
          imageSize:new BMap.Size(60,60),//设置显示图像大小比例
          // 指定定位位置。      
          anchor: new BMap.Size(0, 0),    
          // 设置图片偏移。       
          imageOffset: new BMap.Size(0, 0 - index * 25),    

      });     
      // 创建标注命令Marker
      var marker=new BMap.Marker(point,{icon:myIcon})
      map.addOverlay(marker)

    }
    // 调用
    addMarker(point);

    // 调用自定义标注模块
    customCoverings.coverings(coords,map);
   

     // 点击出现位置信息
     map.addEventListener("click", function(e){    
      var mapPoints = {"lng":e.point.lng,"lat":e.point.lat};  
      // console.log(typeof mapPoints)

      // 存值
      sessionStorage.setItem("mapPointsKey",'{lng:' + mapPoints.lng +',lat:' + mapPoints.lat + '}')
      // console.log(sessionStorage.getItem("mapPointsKey") + "获值")


        
      // var gc = new BMap.Geocoder();//创建地理编码实例
      // // 根据坐标得到地址
      // gc.getLocation(new BMap.Point(mapPoints.lng,mapPoints.lat), function(result){      
      //     if (result){      
      //       // alert(result.address);  
      //       var opts = {    
      //         width : 60,       
      //         height: 40,      
      //         title : "地址:"  // 信息窗口标题   

      //       }    
      //       var infoWindow = new BMap.InfoWindow(result.address, opts);  // 创建信息窗口对象    
      //       map.openInfoWindow(infoWindow, map.getCenter());      // 打开信息窗口    
      //     }      
      // });
      
    }); 

    // console.log(sessionStorage.getItem("mapPointsKey") + "获值2")
    
    // 获取当前位置
    var geolocation = new BMap.Geolocation();
    // console.log(geolocation)
    geolocation.getCurrentPosition(function(r){
      if(this.getStatus() == BMAP_STATUS_SUCCESS){

        //坐标转换完之后的回调函数
       var translateCallback = function (data){
          if(data.status === 0) {
            var marker = new BMap.Marker(data.points[0]);
            map.addOverlay(marker);
            var label = new BMap.Label("转换后的百度坐标（正确）",{offset:new BMap.Size(20,-10)});
            marker.setLabel(label); //添加百度label
            map.setCenter(data.points[0]);

            setTimeout(function(){
              var convertor = new BMap.Convertor();
              var pointArr = [];
              pointArr.push(marker);
              convertor.translate(pointArr, 1, 5, translateCallback())
            }, 1000); 
          }
        }

        


        map.panTo(r.point);
        // console.log('您的位置：'+r.point.lat+','+r.point.lng);

        var point = new BMap.Point(r.point.lng,r.point.lat);//用所定位的经纬度查找所在地省市街道等信息
            var gc = new BMap.Geocoder();
            gc.getLocation(point, function(rs){
               var addComp = rs.addressComponents; 
              //  console.log(rs.address);//输出所在地址信息

            // 信息窗口
            // var opts = {    
            //     width : 60,         
            //     height: 40,         
            //     title : "地址:"  // 信息窗口标题   
            // }    
            // var infoWindow = new BMap.InfoWindow(rs.address, opts);  // 创建信息窗口对象    
            //  map.openInfoWindow(infoWindow, map.getCenter());      // 打开信息窗口
            

        });

      }
      else {
        console.log('failed'+this.getStatus());
      }        
    });


    
    var mapStyleJson=[
              {
                        "featureType": "background",
                        "elementType": "all",
                        "stylers": {
                                  "color": "#131927ff",
                                  "hue": "#121524",
                                  "saturation": 1
                        }
              },
              {
                        "featureType": "road",
                        "elementType": "all",
                        "stylers": {
                                  "color": "#222d41ff",
                                  "hue": "#222d41"
                        }
              },
              {
                        "featureType": "all",
                        "elementType": "labels.text.stroke",
                        "stylers": {
                                  "color":"#f3f3f3ff",
                        }
              },
              {
                        "featureType": "educationlabel",
                        "elementType": "labels.icon",
                        "stylers": {
                                  "color": "#24960eff",
                                  "hue": "#136503"
                        }
              },
              // {
              //   "featureType": "all",
              //   "elementType": "labels.text.fill",
              //   "stylers": {
              //   "color": "#fff",
              //   "visibility": "off"
              //   }
              // }

         
    ]
    // 生成配置地图样式
    map.setMapStyle({styleJson: mapStyleJson });

  }







  module.exports = {
    handlerMap:handlerMap,
  }


