<template>
  <div class="alarmType">
      <!--告警类型分布图 组件复用 -->
     <el-row>
         <el-col>
             <div class="grid-content bg-purple">
                    <h3>
                      {{alarmTypes.title}}
                    </h3>
                    <!-- <button>开始日期至结束日期∨</button> -->
                    <ul>
                      <li ref="alarmTypeChart">

                      </li>
                    </ul>
                </div>
         </el-col>
     </el-row>

  </div>
</template>

<script>

export default {
  name: 'alarmType',

  data () {
    return {
        alarmTypes:{
            title:'告警类型分布图',
            dataTypes:[
                        {value:30, name:'感烟: '},
                        {value:29, name:'燃气: '},
                        {value:5, name:'液位: '},
                        {value:5, name:'电气: '},
                        {value:35, name:'压力: '},
                    ]
        }
    }
  },

  mounted(){
    
        var alarmTypeChart = this.$refs.alarmTypeChart;   
        // this.tables.forEach((table,index) => {
                // 图表初始化
        var myChart1 = this.$echarts.init(alarmTypeChart);

        
        // 配置数据

       var option = {
            tooltip: {
            //    trigger: 'item',
            //    formatter: "{b}:{c}"
            },
            color: ['rgb(58,195,205)','rgb(38,43,233)','rgb(244,34,35)','rgb(245,221,37)','rgb(103,57,205)'],
            series: [
                {
                    type:'pie',
                    radius: ['70%', '100%'],
                    label: {
                        normal: {
                            formatter: '{abg|}\n{b|{b}}{c}',
                            borderColor: '#aaa',
                            borderRadius: 4,
                            color:'#fff',
                            rich: {
                                b: {
                                    fontSize: 16,
                                    lineHeight: 33,
                                    color:'#fff'
                                },
                            }
                        },
                        emphasis: {//hover
                            show: true,
                            textStyle: {
                                fontSize: '20',
                            }
                        }
                    },
                    data:this.alarmTypes.dataTypes,
                }
            ]
        };

                            
        
        // 生成图表 myChart是个数组 
        myChart1.setOption(option);   
        window.addEventListener("resize",function(){
      	 myChart1.resize();
                	}); 
        // })
                    
  }
  
}
</script>


<style lang="less" scoped>
@import "../../assets/style/variable.less";

.alarmType{
      >.el-row{
        >.el-col{
            background: @background-color;
          >.grid-content{
            //   height: 274px;
            >h3{
            padding: 1.25rem 0 0 0.625rem;
            font-size: 22px;
            }
            // 日期按钮选择
            >button{
              padding: 5px 20px;
              letter-spacing: 2px;
		      background: linear-gradient(#111d2b,#0c141f);
              color:#fff;
              border-radius: 4px;
              margin: 30px 0 20px 10px;
              cursor: pointer;
            }
            >ul{
              font-size: 1.125rem;
              padding: 18% 7% 6% 7%;
              flex-wrap:wrap;
              display:flex;
              >li{
                    flex-grow:1;
                    flex-basis:394px;
                    height: 166px;
                    text-align: center;
                    margin-bottom:0.625rem;
                    flex-shrink:1;
                    padding: 10px;
              }

            }
          }
        }
      }
    
  }

</style>






